html,
body {
  font-family: "Courier New", Courier, "Lucida Sans Typewriter", "Lucida Typewriter", monospace;
  margin: 0;
  padding: 0; }

.header {
  position: absolute;
  top: 20px;
  left: 30%; }

.container {
  max-width: 700px;
  height: 60%;
  position: relative;
  overflow: hidden;
  border-radius: 20px;
  -webkit-box-shadow: -7px 8px 15px -2px #9b9765;
  box-shadow: -7px 8px 15px -2px #9b9765;
  margin: 15% 0 0 20px; }

.top-part {
  background: url("./assets/imgs/bg2.png") no-repeat center;
  background-size: cover;
  height: 40%;
  width: 100%;
  box-sizing: border-box; }

.today-detail {
  height: 100%;
  width: 40%;
  box-sizing: border-box;
  position: relative; }

.today-weather {
  text-align: center;
  height: 70%;
  width: 100%;
  padding-top: 10%;
  padding-left: 5%; }

.today-temp {
  margin: 0;
  padding: 0;
  color: white;
  font-size: 4rem;
  text-align: center; }

.today-temp1 {
  color: #b1a9d0; }

.humidity-wind {
  display: flex;
  align-content: center;
  height: 30%;
  width: 100%;
  min-height: 50px;
  text-align: center;
  color: #b1a9d0;
  font-size: 1rem;
  padding-bottom: 30px; }

.humidity {
  flex-grow: 1;
  height: 100%; }

.hw-seperated-line {
  display: inline-block;
  width: 1px;
  height: 12px;
  background: #b1a9d0;
  margin: 10% 0; }

.hw-seperated-line {
  width: 2px;
  height: 50px;
  background: #b1a9d0; }

.wind {
  flex-grow: 1;
  height: 100%; }

.city-location {
  position: absolute;
  top: 5%;
  right: 10%;
  color: white;
  letter-spacing: 2px; }

.city-location::after {
  content: "";
  width: 50%;
  height: 2px;
  display: block;
  margin: 0 auto;
  background: white; }

.link-top {
  width: 100px;
  height: 2px;
  border-top: 2px solid white; }

.botom-part {
  display: flex;
  margin-top: 20px;
  width: 100%;
  height: 60%; }

.left-part {
  height: 100%;
  margin-left: 15px;
  margin-right: 5%;
  flex-grow: 1;
  position: relative; }

.right-part {
  flex-grow: 1;
  display: flex;
  align-items: center;
  width: 65%;
  height: 100%;
  border-left: 2px solid purple;
  padding: auto 3% 15%; }

.dayWeather {
  margin: 10px;
  flex-grow: 1;
  text-align: center; }

.dayWeather h3 {
  font-size: 20px;
  text-align: center; }

.dayWeather h4 {
  text-align: center; }

.dayweather-icons {
  width: 50px;
  height: 50px;
  color: gold; }

.WiRaindrops {
  width: 100px;
  height: 100px; }

.twitter-feed {
  display: flex;
  align-items: baseline; }

.twitter-feed > h3 {
  margin-right: 10px;
  font-size: 20px; }

.cityhashtag {
  color: #3d278d; }

.twitter-logo {
  color: #0099ff;
  margin-right: 10px; }

.twitter-news {
  width: 100%;
  height: 40%; }

.twitter-feed-words {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center; }

.grey-circle {
  color: #dff1ff;
  width: 15%;
  height: 15%;
  margin-right: 15px;
  display: inline-block; }

.twitter-feed-words > p {
  width: 85%;
  word-wrap: break-word;
  word-break: break-word;
  font-size: 14px;
  color: #c0c0d4;
  display: inline-block; }

.next-div {
  display: flex;
  height: 40px;
  width: 100%;
  padding-top: 10px; }

.next-text {
  flex-grow: 1;
  text-align: right;
  display: inline-block;
  flex-grow: 1;
  margin: auto;
  letter-spacing: 5px; }

.next-arrow {
  color: orange;
  flex-grow: 1;
  width: 20%;
  height: 100%; }

@media screen and (max-device-width: 800px) {
  .container {
    min-width: 400px;
    height: 100%; } }
